.c-MainSearchToggle--group {
    margin: 0 0 0 1.5rem;
    align-self: center;
    position: absolute;
    right: 2rem;
    top: 5rem;
}
.c-MainSearchToggle--group label {
    opacity: 0.6;
}
.c-MainSearchToggle--group svg {
    font-size: 1.75rem !important;
    margin: 0.3rem;
}
.c-MainSearchToggle--button {
    color: black;
    background-color: white;
}

.c-MainSearchToggle--button.active {
    opacity: 1;
}

.c-MainSearchToggle--button:hover {
    color: #fff;
    opacity: 1;
}

.c-MainSearchToggle--button.active:hover {
    color: white;
    background-color: inherit;
}

.c-MainSearchToggle--button.focus {
    color: black;
    background-color: white;
}

.c-MainSearchToggle--button .btn {
    border: 0px;
}
